.App {
    width: 100%;
    float: left;
}

.products {
    width: 90%;
    margin-left: 5%;
    margin-right: 5%;
    float: left;
}

.products-main {
    width: 22%;
    margin: 1.5%;
    float: left;
}

.pro-div {
    width: 100%;
    float: left;
    border: solid 1px #ccc;
    text-align: center;
}

.products-sub {
    width: 100%;
    height: 250px;
    position: relative;
}

.products-sub img {
    max-width: 100%;
    max-height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 1%;
    bottom: 0;
    margin: auto;
}

button {
    text-align: center;
    padding: 10px 5px;
    margin-bottom: 15px;
    cursor: pointer;
}

.plus, .minus {
    padding: 7px 12PX;
    border: solid 1px #ccc;
    margin: 3%;
}

.plin {
    margin-bottom: 20px;
}

.basket {
    position: fixed;
    width: 50px;
    height: 53px;
    right: 0;
    top: 1%;
    /* background: rgba(217, 209, 209, 0.44); */
    background: rgba(251, 250, 250, 1);
    box-shadow: 0px 5px 7px #000;
    padding: 0px;
}

.basket h3 {
    text-align: center;
}

.cartItem {
    position: absolute;
    /* right: 0%; */
    width: 288px;
    background: rgba(251, 250, 250, 1);
    box-shadow: 0px 5px 7px #000;
    padding: 0px;
    text-align: center;
    z-index: 1;
    transition: 0.5s ease-in;
    padding: 20px 10px;
    position: fixed;
}

.overlay {
    width: 100%;
    height: 900px;
    background-color: rgba(24, 22, 22, 0.36);
    display: block;
    position: absolute;
    z-index: 1;
}

.cartFinal{
    border-top:  solid 1px #ccc;
    padding-bottom: 25px;
}

